'use client';

import Link from 'next/link';
import { DollarSign, Bike, MapPin, Users } from 'lucide-react';

export default function RentalCompanyDashboard() {
  return (
    <div className="min-h-screen p-8 bg-gray-50">
      <h1 className="text-3xl font-bold mb-6 text-gray-800">üè¢ Rental Company Dashboard</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6 mb-8">
        <DashboardCard icon={<MapPin />} title="Stations" value="4" href="/my-business/stations" />
        <DashboardCard icon={<Bike />} title="eBikes" value="12" href="/my-business/ebikes" />
        <DashboardCard icon={<DollarSign />} title="Revenue (This Month)" value="$2,340" href="/my-business/revenue" />
        <DashboardCard icon={<Users />} title="Staff" value="6" href="/my-business/staff" />
      </div>

      <div className="bg-white rounded-2xl shadow p-6 border border-gray-200">
        <h2 className="text-xl font-semibold text-gray-800 mb-4">Quick Actions</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
          <QuickAction label="Add New Station" href="/my-business/stations" />
          <QuickAction label="Create eBike Model" href="/my-business/ebike-models" />
          <QuickAction label="Assign Staff" href="/my-business/staff" />
        </div>
      </div>
    </div>
  );
}

function DashboardCard({ icon, title, value, href }: { icon: JSX.Element; title: string; value: string; href: string }) {
  return (
    <Link href={href} className="bg-white p-4 rounded-xl shadow hover:shadow-lg transition border border-gray-200 flex items-center gap-4">
      <div className="text-[#00d289] bg-[#e6fff5] rounded-full p-2">
        {icon}
      </div>
      <div>
        <p className="text-sm text-gray-500">{title}</p>
        <h3 className="text-lg font-bold text-gray-800">{value}</h3>
      </div>
    </Link>
  );
}

function QuickAction({ label, href }: { label: string; href: string }) {
  return (
    <Link href={href} className="block bg-[#00d289] hover:bg-[#00b67a] text-white text-center font-medium px-4 py-3 rounded-xl transition">
      {label}
    </Link>
  );
}
